}

.tableBox {
    margin-top: 40px;
}
.table {
    margin-top: 40px;
    width: 100%;
	border: none;
}

.table thead th {
	font-weight: bold;
	text-align: left;
	border: none;
	padding: 10px 15px;
	background-color: #eadec6;
    color:#03162f ;
	font-size: 26px;
}
.table thead tr th:first-child {
	border-radius: 8px 0 0 8px;
}
.table thead tr th:last-child {
	border-radius: 0 8px 8px 0;
}
.table tbody td {
 
	text-align: left;
	border: none;
	padding: 10px 15px;
	font-size: 24px;
	vertical-align: top;
}
.table tbody tr:nth-child(even){
    background-color: #eadec6;
    color:#03162f ;
}
.table tbody tr td:first-child {
	border-radius: 8px 0 0 8px;
}
.table tbody tr td:last-child {
	border-radius: 0 8px 8px 0;
}